.banner {
  color: white;
  object-fit: contain;
  height: 448px;
}
.banner__contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}
.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}
.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
}
.banner__button:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.5s ease-in-out;
}
.banner--fadeBottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}
@media (max-width: 768px) {
  .banner {
    height: 100%;
  }

  .banner__contents {
    margin-left: 10px;
    padding-top: 80px;
    height: auto;
  }

  .banner__title {
    font-size: 2rem;
  }

  .banner__description {
    width: auto;
    max-width: 90%;
    height: auto;
    font-size: 0.8rem;
  }

  /* Adjust button styles for mobile */
  .banner__button {
    padding-left: 1rem;
    padding-right: 1rem;
    margin-right: 0.5rem;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
  }
}
